import * as React from "react"
import { navigate } from 'gatsby'
import { css } from '@emotion/react'
import { SEO } from '../components/seo'
import PageContent from '../components/pageContent/pagecontent'
import { IllustrationEmptySearch, Text, Box, theme, Button } from 'fusion'


const contentStyle = css({
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
  marginTop: "48px",
  textAlign: "center",
  height: "calc(100vh - 206px)",
  "@media (max-width: 780px)": {
    marginTop: "0px"
  }
})

const illustrationStyle = {
  width: "100%",
  maxWidth: "320px"
}


// markup
const NotFoundPage = () => {
  return (
    <>
      <SEO title="404" />
      <PageContent>
        <Box css={contentStyle}>
          <IllustrationEmptySearch style={illustrationStyle}></IllustrationEmptySearch>
          <Text mt={2} weight="semibold" variant='heading4'>404 - That's an error</Text>
          <Text as="p"  mt={1} style={{ color: theme.colors.textSecondary }}>Sorry, we couldn't find that page.</Text>
          <Button  onClick={()=>{navigate("/")}} variant="secondary" mt={3}>Go home</Button>
        </Box>
      </PageContent>
    </>
  )
}

export default NotFoundPage